import React from "react";
import { Layout, LayoutWithSidebar } from '../components/Layout';
import { IHeaderProps } from '../components/Header';
import { FaCogs } from "react-icons/fa";
import { ApiV1 } from '../misc/Api_v1';
import "./Api.sass"
import { ApiV2 } from '../misc/Api_v2';
import { constants } from '../constants';

export class ApiPage extends React.Component<any, {apiVersion: string}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      apiVersion: "v2"
    }
  }
  
  handleChange = (ev) => {
    let s = {};
    s[ev.target.name] = ev.target.value;
    this.setState(s);
  }
  render()
  {
    let header: IHeaderProps = {
      url: "/",
      type: "website",
      title: "Power Query Formatter | Beautify your PowerQuery code",
      description: "Report a bug for M Formatter"
    };
    return (
      <LayoutWithSidebar header={header}>
        <div className="api-page">
          <h1 className="api-page-header">
            <div>
              <FaCogs style={{position: "relative", top: ".15em"}} /> Api Documentation
            </div>
            <div className="option">
              <div style={{display: "flex", alignItems: "center"}}>
                <label>
                  <span title="Api Version">Version</span>
                </label>
                <div className="select is-small" style={{marginLeft: "10px"}}>
                  <select onChange={this.handleChange} value={this.state.apiVersion} name="apiVersion">
                    <option value={"v1"} title="v1">v1</option>
                    <option value={"v2"} title="v2">v2</option>
                  </select>
                </div>
              </div>
            </div>
          </h1>
          {
            this.state.apiVersion == "v1"?  (
              <div className="deprecated-msg"><b>v1 Api is deprecated. Use the v2 api to access more features and better format results.</b></div>
            ) : ""
          }
          <div>
            The formatting service is hosted as an azure function. Initial use will probably take some time (due to the warming of the azure function) but subsequent usages should be much quicker. 
          </div>
          <div>
            <h3>Terms of use</h3>
            You are free to use the api as long as you give credit and (if you use the formatter for your website) embed a backlink to <b>{constants.backlinkUrl}</b> . If you use the api you agree to the following conditions:
            <ul>
              <li>The service is provided "as is" without warranty of any kind</li>
              <li>We do not save any queries or any meta data with the exception of Error tracking. We track Errors that occur on during formatting in order to fix bugs and improve our service. In these cases meta data about the Error is sent to the azure app insights remote service which contains a stacktrace. This stacktrace might <b>indirectly</b> contain some information about the query that caused the error</li>
            </ul>
            We provide our service as is and give no warranty about the results
          </div>
          <div>
            <h3>How to use</h3>
            {
              this.state.apiVersion == "v1" ? <ApiV1/> : <ApiV2/> 
            }
          </div>
        </div>
      </LayoutWithSidebar>
    );
  }
}

export default ApiPage;