import React from "react";
import { constants } from '../constants';

export class ApiV2 extends React.Component<any,{bodyDoc: "json"|"ts"}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      bodyDoc: "ts"
    }
  }
  
  handleChange = (ev) => {
    let s = {};
    s[ev.target.name] = ev.target.value;
    this.setState(s);
  }
  
  render() 
  {
    return (
    <div className="api-doc-body">
      <div>
        <div>
          To use the api you need to send a <b>POST</b> request to 
          <b> {constants.formatterApiUrl_v2} </b>. 
          A javascript usage example is provided in <a href="https://github.com/UliPlabst/powerquery-formatter-examples">our example repository.</a><br/>
          The body of the request is a flat object and can either be provided by a Content-Type <i>application/x-www-form-urlencoded</i> or <i>application/json</i>.
          Required fields are only <b>code</b> and <b>resultType</b>. The resultType determines which additional non required options you can provide to the formatter and what kind of result you want to have. The property resultType can have 4 values
          <ul>
            <li><b>text</b>: returns a json object that contains the formatted plain text (Content-Type: application/json)</li>
            <li><b>html</b>: returns a json object that contains the formatted code as HTML (Content-Type: application/json)</li>
            <li><b>codeblock</b>: returns a json object that contains the formatted HTML embedded in a codeblock (Content-Type: application/json). Note that the emitted codeblock HTML needs to be styled by your embedding page. You can have a look at the styles we use for the codeblock with a dark and light theme <a href="https://github.com/mogular/powerquery-formatter-examples/blob/master/src/codeblock.sass">here (sass)</a> or <a href="https://github.com/mogular/powerquery-formatter-examples/blob/master/src/codeblock.css">here (css)</a></li>
            <li><b>iframe</b>: returns a full HTML page that contains the formatted HTML embedded in a codeblock (Content-Type: text/html)</li>
          </ul>
          <h3>Full definition of the request body</h3>
          <div className="option">
            <div style={{display: "flex", alignItems: "center"}}>
              <label>
                <span title="Api Version">Definition</span>
              </label>
              <div className="select is-small" style={{marginLeft: "10px"}}>
                <select onChange={this.handleChange} value={this.state.bodyDoc} name="bodyDoc">
                  <option value={"ts"} title="typescript type definition">typescript</option>
                  <option value={"json"} title="json schema definition">json schema</option>
                </select>
              </div>
            </div>
          </div>
          <div className="api-body-def">
            {
              this.state.bodyDoc == "json" ? (
                <div>
                  <BodyRequestSchema></BodyRequestSchema>
                </div>
              ) : (
                <div>
                  The api body is defined by the type definition of <b>IApiRequestBody</b>
                  <BodyRequestTsDef></BodyRequestTsDef>
                </div>
              )
            }
          </div>
        </div>
      </div>
      <div>
        <h3>Response</h3>
        If you chose resultType = iframe you will receive a response with Content-Type <i>text/html</i> containing a whole HTML page that can be embedded. All other resultTypes will yield in a json response object that contains the following properties:
        <ul>
          <li><b>result </b>(string): The formatted code result (can be text or html string)</li>
          <li><b>errors </b>(IApiError[]): An array of errors</li> 
          <li><b>success </b>(boolean): true when no errors happened. When this is true the errors array is filled. When this is false errors array is null.</li> 
        </ul>
        Definition of the returned error:
        <div>
          <ErrorDef></ErrorDef>
        </div>
      </div>
      <h3>Styling</h3>
      For all resultTypes but text the formatter applies css classes to the html that you can use to style the formatted html code. You can also provide your style sheet via the inlineCss option (not for resultType = iframe) so the formatter applies the styles to the html using html inline styling.
      The available css classes are 
      <ul>
        <li>comment</li>
        <li>constant
        <ul>
          <li>keyword</li>
          <li>identifier</li>
          <ul>
            <li>method-call</li>
          </ul>
          <li>operator</li>
          <ul>
            <li>operator-dot</li>
            <li>operator-keyword</li>
          </ul>
          <li>bracket</li>
          <li>bracket-{'{'}num{'}'}</li>
          <li>type</li>
        </ul>
          </li>
          <li>literal
          <ul>
          <li>string</li>
          <li>list</li>
          <li>boolean</li>
          <li>number</li>
          <li>null</li>
          <li>record</li>
          <li>string</li>
        </ul>
        </li>
      </ul>
    </div>
    )
  }  
}


