import React from "react";
import { constants } from '../constants';

export class ApiV1 extends React.Component<any,any>
{
  render() 
  {
    return (
    <div>
      <div>
        <div>
          To use the api you need to send a <b>POST</b> request to 
          <b> {constants.formatterApiUrl_v1} </b> 
          with content type <b>application/json</b> and the following body structure (typescript type declaration):
          <div dangerouslySetInnerHTML={{__html: `<pre class="hljs" style="display: block; overflow-x: auto; padding: 0.5em; color: rgb(51, 51, 51); background: rgb(248, 248, 248) none repeat scroll 0% 0%;"><span class="hljs-keyword" style="color: rgb(51, 51, 51); font-weight: 700;">type</span> RequestBody =
{
    code: <span class="hljs-built_in" style="color: rgb(0, 134, 179);">string</span>,
    config?: IConfig,   <span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//optional, will use default config if not supplied</span>
    resultType?: <span class="hljs-string" style="color: rgb(221, 17, 68);">"html"</span> | <span class="hljs-string" style="color: rgb(221, 17, 68);">"text"</span> <span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//optional, default is "html". If set to "text", the resulting html will be converted to plaintext using html2plaintext.</span>
}</pre>`}}>
          </div>
        </div>
        A javascript usage example is provided in <a href="https://github.com/UliPlabst/powerquery-formatter-examples">our example repository.</a><br/>
        The config object is defined by
        <div dangerouslySetInnerHTML={{__html: `<pre class="hljs" style="display: block; overflow-x: auto; padding: 0.5em; background: rgb(240, 240, 240); color: rgb(68, 68, 68);"><span class="hljs-keyword" style="font-weight: 700;">export</span> <span class="hljs-keyword" style="font-weight: 700;">interface</span> IConfig
{
debugPrint?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
debugCursor?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
debug?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//String that is used for indentation, default = "&amp;nbsp;&amp;nbsp;"</span>
indentation?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">string</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Length of the indentation string (is used for determining when the max line width is reached)</span>
indentationLength?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">number</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//String that is used to terminate a line, default = "&lt;br/&gt;"</span>
lineEnd?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">string</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//String that is used as a whitespace, default = "&amp;nbsp;"</span>
ws?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">string</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Maximum line width, default = 100. The formatter forces expression to break into several lines if the maximum line width would be reached. Please not some expressions cannot be broken into multiple lines such as very long identifiers and therefore the maximum line width can be exceeded in these cases. As of now there is no warning to give you info about that.</span>
lineWidth?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">number</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Number of different bracket classes, default = 3. The formatter assigns bracket pairs the classes 'bracket' and 'bracket-{num}' where num = 1...{numBrackets}. You can then style your matching brackets with configurable depth.</span>
numBrackets?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">number</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//All text (code) is escaped into html format, default = true.</span>
escapeHtmlText?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//[DEPRECATED, use "alignPairedLetExpressionsByEqual" and "alignPairedReocrdExpressionsByEqual"] Align paired expressions by their equal sign, default = false.</span>
alignPairedExpressionsByEqual?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Align paired expressions by their equal sign, default = false.</span>
alignPairedLetExpressionsByEqual?: AlignmentStrategy;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Align paired expressions by their equal sign, default = false.</span>
alignPairedRecordExpressionsByEqual?: AlignmentStrategy;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//If null the formatter will put classes onto the resulting html but not styles (can be used for websites where you want to style the formatted html with css yourself). If non null the classes will be translated to inline styles on the html elements. See below for more information about the used classes.</span>
inlineCss?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">string</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Toggles inline css usage;</span>
inlineCssToggle?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Includes comments in the format results, default = true</span>
includeComments?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Align line comments</span>
alignLineCommentsToPosition?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">number</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Indent section members</span>
indentSectionMembers?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Newline after CSV under LetExpression</span>
newLineCsvInLet?: <span class="hljs-built_in" style="color: rgb(57, 115, 0);">boolean</span>;
}</pre>`}}>
        </div>
        <div>
          Available alignment strategy options are
          <div dangerouslySetInnerHTML={{
            __html: `
            <pre class="hljs" style="display: block; overflow-x: auto; padding: 0.5em; background: rgb(240, 240, 240); color: rgb(68, 68, 68);"><span class="hljs-keyword" style="font-weight: 700;">export</span> <span class="hljs-keyword" style="font-weight: 700;">enum</span> AlignmentStrategy
{
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Never align</span>
never = <span class="hljs-string" style="color: rgb(136, 0, 0);">"never"</span>,
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Always align</span>
always = <span class="hljs-string" style="color: rgb(136, 0, 0);">"always"</span>,
<span class="hljs-comment" style="color: rgb(136, 136, 136);">//Only align when all expressions in the current scope are single line and won't exceed their line length when aligned by the equal sign.</span>
singleline = <span class="hljs-string" style="color: rgb(136, 0, 0);">"singleline"</span>
}</pre>`
          }}></div>
        </div>
        </div>
      <div>
        The css classes assigned by the formatter that can be used to customize the style either via the <b>inlineCss</b> config option or via css on your webpage are 
      </div>
      <ul>
        <li>comment</li>
        <li>constant
        <ul>
          <li>keyword</li>
          <li>identifier</li>
          <ul>
            <li>method-call</li>
          </ul>
          <li>operator</li>
          <ul>
            <li>operator-dot</li>
            <li>operator-keyword</li>
          </ul>
          <li>bracket</li>
          <li>bracket-{'{'}num{'}'}</li>
          <li>type</li>
        </ul>
          </li>
          <li>literal
          <ul>
          <li>string</li>
          <li>list</li>
          <li>boolean</li>
          <li>number</li>
          <li>null</li>
          <li>record</li>
          <li>string</li>
        </ul>
        </li>
      </ul>
      <div>
        <h3>Response</h3>
        <div>
          You will receive a response with Status 200 on success or Status 400 if an error occurred. 
          In both cases the response body is a json object of type
          <div dangerouslySetInnerHTML={{
            __html: `<pre class="hljs" style="display: block; overflow-x: auto; padding: 0.5em; color: rgb(51, 51, 51); background: rgb(248, 248, 248) none repeat scroll 0% 0%;"><span class="hljs-keyword" style="color: rgb(51, 51, 51); font-weight: 700;">type</span> ResponseBody = {
result: <span class="hljs-built_in" style="color: rgb(0, 134, 179);">string</span>, <span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//HTML format result</span>
errors: FormatError[]
}
            
<span class="hljs-keyword" style="color: rgb(51, 51, 51); font-weight: 700;">type</span> FormatError = {
kind: FormatErrorKind,
innerError?: <span class="hljs-built_in" style="color: rgb(0, 134, 179);">Error</span>,   <span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//the inner exception if there was one</span>
meta?: <span class="hljs-built_in" style="color: rgb(0, 134, 179);">any</span>,            <span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//additional meta information,</span>
message: <span class="hljs-built_in" style="color: rgb(0, 134, 179);">string</span>
}
            
<span class="hljs-keyword" style="color: rgb(51, 51, 51); font-weight: 700;">export</span> <span class="hljs-keyword" style="color: rgb(51, 51, 51); font-weight: 700;">type</span> FormatErrorKind = 
<span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//an unknown error occurred. If this happens look at the innerError and possibly report the bug</span>
<span class="hljs-string" style="color: rgb(221, 17, 68);">"UNKNOWN"</span>             
<span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//parsing was unsuccessfull. As we use the official ms parser the problem is most likely within your code</span>
| <span class="hljs-string" style="color: rgb(221, 17, 68);">"PARSER_ERROR"</span>      
<span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//formatting was unsuccessfull. This is a strong indication of a bug, consider writing a bug report if this happens</span>
| <span class="hljs-string" style="color: rgb(221, 17, 68);">"FORMATTER_ERROR"</span>   
<span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//the custom css that you provided is invalid</span>
| <span class="hljs-string" style="color: rgb(221, 17, 68);">"CSS_ERROR"</span>         
<span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//you provided an invalid config object</span>
| <span class="hljs-string" style="color: rgb(221, 17, 68);">"INVALID_CONFIG"</span>    
<span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//the formatter encountered an unknown node kind in the AST. Please note that section documents are currently not supported.</span>
<span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//If this happens for code without sections then we are missing a node kind implementation. There are probabably some that we are missing but since our test bench does not include any of them it is hard for me to track them down. </span>
<span class="hljs-comment" style="color: rgb(153, 153, 136); font-style: italic;">//If you provide your query (or at least the portion of it that causes the error) it would help me out.</span>
| <span class="hljs-string" style="color: rgb(221, 17, 68);">"UNKNOWN_ENTITY"</span>;  </pre>`}}></div>
        </div>
      </div>
    </div>
    )
  }  
}